import React, { Component } from 'react';
import './App.css';

class App extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      players: [],
      player1: '',
      player2: '',
      winners: []
    }

    this.addPlayers = this.addPlayers.bind(this);
    this.savePlayer1 = this.savePlayer1.bind(this);
    this.savePlayer2 = this.savePlayer2.bind(this);
    this.recordWinner = this.recordWinner.bind(this);
  }

  addPlayers(player) {
    this.setState({
      players: [...this.state.players, player]
    })
  }

  savePlayer1(player) {
    this.setState({
      player1: player
    })
  }

  savePlayer2(player) {
    this.setState({
      player2: player
    })
  }

  recordWinner(winner) {
    if (this.state.winners.filter(e => e.player === winner).length > 0) {
      let winningPlayer = this.state.winners.find(e => e.player === winner);
      winningPlayer.wins++;
      this.setState({
        winners: [...this.state.winners]
      })
    } else {
      this.setState({
        winners: [...this.state.winners, {'player': winner, 'wins': 1}]
      })
    }
  }

  render() {
    return (
      <div className="App">
        <header>
          <h2>Pool Game Tracking App</h2>
        </header>
        <main className="container">
          <CreatePlayer className="row" addPlayers={this.addPlayers}/>
          <PlayersList className="row" players={this.state.players}/>
          <SelectPlayers className="row" players={this.state.players} savePlayer1={this.savePlayer1} savePlayer2={this.savePlayer2}/>
          <ChooseWinner className="row" player1={this.state.player1} player2={this.state.player2} recordWinner={this.recordWinner}/>
          <Leaderboard className="row" winners={this.state.winners}/>
        </main>
      </div>
    );
  }
}

class CreatePlayer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      player: ''
    };

    this.handleChange = this.handleChange.bind(this);
    this.createPlayer = this.createPlayer.bind(this);
  }

  handleChange(e) {
    this.setState({
      player: e.target.value
    });
  }

  createPlayer(e) {
    e.preventDefault();
    this.props.addPlayers(this.state.player);
    this.setState({
      player: ''
    });
  }

  render() {
    return (
      <div className="block--margin-top">
        <h3>1. Create Players</h3>
        <div className="row">
          <div className="col-md"></div>
          <div className="col-md">
            <form onSubmit={this.createPlayer}>
              <input className="form-control" type="text" size="40" placeholder="Type player's name then press ENTER" value={this.state.player} onChange={this.handleChange}/>
            </form>
          </div>
          <div className="col-md"></div>
        </div>
      </div>
    );
  }
}

function PlayersList(props) {
  return (
    <ul className="flex-container">
      {props.players
      .filter((player, index, array) => array.indexOf(player) === index)
      .map((player, index) => {
        return (
          <li key={index}>{player}</li>
        )
      })}
    </ul>
  )
}

class SelectPlayers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      player1: '',
      player2: ''
    }

    this.selectPlayer1 = this.selectPlayer1.bind(this);
    this.selectPlayer2 = this.selectPlayer2.bind(this);
  }

  selectPlayer1(e) {
    this.setState({
      player1: e.target.value
    }, () => {
      this.props.savePlayer1(this.state.player1)
    })
  }

  selectPlayer2(e) {
    this.setState({
      player2: e.target.value
    }, () => {
      this.props.savePlayer2(this.state.player2)
    })
  }

  render() {
    return (
      <div className="block--margin-top">
        <h3>2. Select Players</h3>
        <div className="row">
          <div className="col-md"></div>
          <div className="col-md">
            <select className="form-control" value={this.state.player1} onChange={this.selectPlayer1}>
              <option value="">Choose player 1</option>
              {this.props.players
              .filter((player, index, array) => array.indexOf(player) === index)
              .filter((player) => player !== this.state.player2)
              .map((player, index) => {
                return (
                  <option key={index} value={player}>
                    {player}
                  </option>
                )
              })}
            </select>
            <h4>VS</h4>
            <select className="form-control" value={this.state.player2} onChange={this.selectPlayer2}>
              <option value="">Choose player 2</option>
              {this.props.players
              .filter((player, index, array) => array.indexOf(player) === index)
              .filter((player) => player !== this.state.player1)
              .map((player, index) => {
                return (
                  <option key={index} value={player}>
                    {player}
                  </option>
                )
              })}
            </select>
          </div>
          <div className="col-md"></div>
        </div>
      </div>
    )
  }
}

class ChooseWinner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      winner: 'Choose winner'
    }
    this.selectWinner = this.selectWinner.bind(this);
    this.recordScore = this.recordScore.bind(this);
  }

  selectWinner(e) {
    this.setState({
      winner: e.target.value
    })
  }

  recordScore() {
    if (this.state.winner !== '' && this.state.winner !== 'Choose winner') {
      this.props.recordWinner(this.state.winner)
    }
  }

  render() {
    return (
      <div className="block--margin-top">
        <h3>3. Select Winner</h3>
        <div className="row">
          <div className="col-md"></div>
          <div className="col-md">
            <select className="form-control" value={this.state.winner} onChange={this.selectWinner}>
              <option value="">Choose winner</option>
              <option value={this.props.player1}>
                {this.props.player1}
              </option>
              <option value={this.props.player2}>
                {this.props.player2}
              </option>
            </select>
            <div className="block--margin-top">
              <button className="btn btn-success btn-lg btn-block" onClick={this.recordScore}>Record Score</button>
            </div>
          </div>
          <div className="col-md"></div>
        </div>
      </div>
    )
  }
}

function Leaderboard(props){
  return (
    <div className="block--margin-top">
      <div className="row">
        <div className="col-md"></div>
        <div className="col-md leaderboard">
          <h3>Leaderboard</h3>
          <hr/>
          <ol>
            {props.winners
            .filter((winner, index, array) => array.indexOf(winner) === index)
            .sort((a,b) => (b.wins - a.wins))
            .map((winner, index) => {
              return (
                <li key={index}>
                  {winner.player} ({winner.wins})
                </li>
              )
            })}
          </ol>
        </div>
        <div className="col-md"></div>
      </div>
    </div>
  )
}

export default App;
